.home{
    background-color: #052142;
    height: 90vh;
    display: flex;

}
.home-intro{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-left: 100px;
    font-size: 50px;
    color: white;
}
.home-image{
    width: 50%;
    height: 80vh;

}
.spiderman-image{
    margin: 50px auto;height: 80vh;
}
.title{
    animation-name: moveRight;
    animation-duration: 0.7s;


}
.titleTwo{
    animation-name: moveLeft;
    animation-duration: 1s;
}
.titleThree{
    animation-name: moveRight;
    animation-duration: 1.3s;
}
text{
    font-size: 40px;
    color: white;
    margin-bottom: 20px;
}
.button{
    font-size: 16px;
    border: 2px solid black;
    border-radius: 5px;
    outline: none;
    background-color: green;
    padding: 12px 25px;
    position: relative;
    transform: translateZ(0);
    animation-name: moveUp;
    animation-duration: 1s;
    cursor: pointer;

}
.button:before{
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: yellow;
    transform: scaleX(0);
    transition: 30ms ease-out;
}
.button:hover:before{
    transform: scaleX(1);

}
@keyframes moveRight {
    0%{
        opacity: 0;
        transform: translateX(100px);

    }
    100%{
        opacity: 1;
        transform: translateX(0);

    }
}
@keyframes moveLeft {
    0%{
        opacity: 0;
        transform: translateX(-150px);

    }
    100%{
        opacity: 1;
        transform: translateX(0);

    }
}
@keyframes moveUp {
    0%{
        opacity: 0;
        transform: translateY(50px);

    }
    100%{
        opacity: 1;
        transform: translateX(0);

    }
}
@media screen and (max-width:1100px) {
    .home-intro{
     width: 100vw;
    height: 80vh;
    display: flex;
    justify-content: center;
    font-size: 50px;
    margin: 0 auto;
    padding-left: 80px;

    }
    .home-image{
        display: none;
    }
    .button{
        padding: 10px 20px;

    }
    
} 
