.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem ;
}

.nav-memu{
    display: flex;
}
.nav-memu li{
    padding: 0 1rem;
}
.nav-memu li a{
    font-size: 1.2rem;
     font-weight: 500;
     text-decoration: none;

}
.hamburger{
    display: none;
}

@media screen and (max-width:1040px) {
    .nav-memu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(74, 117, 209, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index:-3;
        transition: 0.3s;


    }
    .nav-memu.active{
        left: 0 ;
    }
    .nav-memu li{
        padding: 1rem 0;


    }
    .nav-memu li a{
        font-size: 2rem;
    }
    .hamburger{
        display: initial;
    }
    
}